import React, { Component, useState } from 'react';
import Button from 'react-bootstrap/Button';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import ListGroup from 'react-bootstrap/ListGroup';
import Modal from 'react-bootstrap/Modal';
import api from 'utils/api/api';
import { ReactNode } from 'react-bootstrap/node_modules/@types/react';

type MyProps = { author: string; show: boolean; onHide: any; onShowPacks: any };
type MyState = { authorData: any };

export default class AuthorModal extends Component<MyProps, MyState> {
  constructor(params: MyProps) {
    super(params);
    this.state = { authorData: { links: [] } };
  }

  async componentDidUpdate(prevProps) {
    if (this.props.author !== prevProps.author) {
      try {
        const author = await api.getAuthor(this.props.author);
        this.setState({ authorData: author });
      } catch (err) {
        this.setState({
          authorData: undefined,
        });
      }
    }
  }

  render() {
    const viewPacksButton = (
      <Button variant="secondary" onClick={this.props.onShowPacks}>
        View Packs
      </Button>
    );

    let body = (
      <Modal.Body>
        <h4>No profile set!</h4> {viewPacksButton}
      </Modal.Body>
    );

    if (this.state.authorData) {
      let links: ReactNode = undefined;
      if (
        this.state.authorData.links &&
        this.state.authorData.links.length > 0
      ) {
        const linkList = this.state.authorData.links.map((linkData, index) => {
          return (
            <ListGroup.Item key={`authormodal-link-${index}`}>
              <a href={linkData.link} target="_blank">
                {linkData.label}
              </a>
            </ListGroup.Item>
          );
        });

        links = (
          <div>
            <h4 className="mt-4">Links</h4>{' '}
            <ListGroup className="shadow">{linkList}</ListGroup>
          </div>
        );
      }

      let donateButton: ReactNode = undefined;
      if (this.state.authorData.donateLink) {
        donateButton = (
          <Button
            variant="secondary"
            href={this.state.authorData.donateLink}
            target="_blank"
          >
            Donate
          </Button>
        );
      }

      let footer: ReactNode = undefined;

      if (donateButton) {
        footer = (
          <Row className="mt-4">
            <Col>{viewPacksButton}</Col>
            <Col className="d-flex justify-content-end">{donateButton}</Col>
          </Row>
        );
      } else {
        footer = <div className="mt-4">{viewPacksButton}</div>;
      }

      body = (
        <Modal.Body>
          <p>{this.state.authorData.blurb}</p>
          {links}
          {footer}
        </Modal.Body>
      );
    }

    return (
      <Modal
        closeVariant="white"
        show={this.props.show}
        size="lg"
        aria-labelledby="contained-modal-title-vcenter"
        onHide={this.props.onHide}
        centered
      >
        <Modal.Header closeButton closeVariant="white">
          <Modal.Title id="contained-modal-title-vcenter">
            {this.props.author}
          </Modal.Title>
        </Modal.Header>
        {body}
      </Modal>
    );
  }
}
