import React, { useState, useContext } from 'react';
import Card from 'react-bootstrap/Card';
import styled from 'styled-components';
import api from '../../../utils/api/api';
import AppContext from '../../UserProvider';
import PackLink from './PackLink';

const FavoriteWrapper = styled.div`
  position: absolute;
  top: 8px;
  left: 8px;
  color: #d4af37;

  &:hover {
    color: yellow;
    cursor: pointer;
  }
`;

type MyProps = {
  name: string;
  id: string;
  isApproved?: boolean;
  isFeatured?: boolean;
  preview?: boolean;
};

export default function Title(props: MyProps) {
  const appContext = useContext(AppContext);
  const isFavorite =
    appContext.user &&
    appContext.user.favorites.find(pack => pack.id === props.id);

  const ribbonClass = !props.isApproved ? 'ribbon-unapproved' : 'ribbon';
  const ribbonText = !props.isApproved ? 'UNAPPROVED' : 'Featured';
  const hasRibbon = !props.preview && (props.isFeatured || !props.isApproved);

  const favoriteStarClass = isFavorite
    ? 'fas fa-star fa-lg'
    : 'far fa-star fa-lg';
  const favoriteStar =
    !props.preview && appContext.user != null ? (
      <FavoriteWrapper
        onClick={async () => {
          await api.updateFavorite(props.id, !isFavorite);
          await api.getUser();
          appContext.setUser(api.currentUser);
        }}
      >
        <i className={favoriteStarClass}></i>
      </FavoriteWrapper>
    ) : null;
  if (hasRibbon) {
    return (
      <Card.Title>
        {favoriteStar}
        <div className="ribbon-wrapper">
          <div className={ribbonClass}>{ribbonText}</div>
        </div>
        {props.name}
        <PackLink id={props.id}></PackLink>
      </Card.Title>
    );
  } else {
    return (
      <Card.Title>
        {favoriteStar}
        {props.name}
        {!props.preview && <PackLink id={props.id}></PackLink>}
      </Card.Title>
    );
  }
}
